import { ContentSource } from "@rp/react/tanstack-api/content"
import { UseQueryResult } from "@tanstack/react-query"
import { SourceSelectorSelectedSourceInfo } from "../sourceSelector.types"

function mapContentSourceToSelectedSource(
  contentSourceResponse: UseQueryResult<ContentSource, Error>,
): SourceSelectorSelectedSourceInfo {
  if (!contentSourceResponse.data) {
    return { id: "", name: "", packageIDs: [] }
  }

  return {
    id: contentSourceResponse.data.id,
    name: contentSourceResponse.data.name,
    packageIDs: contentSourceResponse.data.packageIds || [],
  }
}

export { mapContentSourceToSelectedSource }
